.main {
  display: flex;
  flex-direction: column;
  border: 2px solid rgba(0,0,0,0.2);
  padding: 4px;
  position: absolute;
  left: 10px;
  top: calc(90px + 48px);
  z-index: 1000;
  border: 2px solid rgba(0,0,0,0.2);
  background-color: white;
}

.main .preference {
  display: block;
  margin: 4px;
}

.main label {
  font-size: 0.8em;
  display: block;
  font-weight: 700;
}

.main select {
  display: block;
}

.main .preferenceButton {
  display: flex;
  align-content: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  padding: 2px;
}

.main .preferenceButton.expanded {
  filter: invert();
}

.main .preferenceButton .caret {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: rotate(45deg);
}
